@import url("https://fonts.googleapis.com/css?family=Noto+Sans+KR:400,500,700&display=swap&subset=korean");html, body, li, ol, ul, strong, em, i, b, button, select, input, article, aside, canvas, figcaption, figure, footer, header { margin: 0; padding: 0; border: 0; }body { font-family: 'Noto Sans KR', sans-serif; font-size: 1rem; line-height: 1.25; color: #454545; }header, main, section, article, nav, footer, address { display: block; }.blind { position: absolute; width: 1px; height: 1px; margin: -1px; overflow: hidden; clip: rect(0, 0, 0, 0); }ul, li, ol { list-style: none; }i, em { font-style: normal; }a { text-decoration: none; }img { max-width: 100%; vertical-align: top; }button, input, select, textarea { border: 0; background-color: transparent; border-radius: 0; }button > span { position: relative; }table { width: 100%; border-collapse: 0; table-layout: fixed; }#skip a { position: absolute; top: -50px; right: 0; left: 0; background-color: #005c83; color: #fff; font-size: 21px; height: 50px; line-height: 50px; text-align: center; }#skip a:focus { top: 0; }